dding: 7px;\r\n  width: 100%;\r\n}\r\n\r\n.add__review__input {\r\n  min-width: 45px;\r\n  min-height: 45px;\r\n  border-radius: 10px;\r\n  border: none;\r\n  padding: 5px;\r\n  outline: none;\r\n  font-size: 1em;\r\n}\r\n\r\n.add_review_btn {\r\n  background: rgb(255, 255, 195);\r\n  padding: 5px;\r\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\r\n  display: inline-block;\r\n  border-radius: 5px;\r\n  transition: all 0.3s ease-in-out;\r\n  min-width: 45px;\r\n  min-height: 45px;\r\n  font-size: 1em;\r\n  border: white;\r\n}\r\n.add_review_btn:active {\r\n  background: rgb(233, 93, 53);\r\n  color: rgb(255, 255, 195);\r\n  box-shadow: 2px 4px 2px rgb(255, 255, 195);\r\n  transition: none;\r\n}\r\n\r\n/* category */\r\n.category ul {\r\n  display: flex;\r\n  flex-direction: row;\r\n  flex-wrap: no-wrap;\r\n  justify-content: flex-end;\r\n  gap: 10px;\r\n  list-style: none;\r\n  margin: 5px;\r\n  margin-bottom: 10px;\r\n  line-height: 1.12em;\r\n}\r\n\r\n.category__item {\r\n  background: rgb(255, 255, 195);\r\n  padding: 5px;\r\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\r\n  display: inline-block;\r\n  border-radius: 5px;\r\n  transition: all 0.3s ease-in-out;\r\n  min-width: 45px;\r\n  min-height: 45px;\r\n  font-size: 1em;\r\n  border: white;\r\n  text-decoration: none;\r\n  padding: 15px;\r\n}\r\n\r\n.toast {\r\n  position: fixed;\r\n  top: 60px;\r\n  right: 0;\r\n  padding: 5px;\r\n  display: inline-block;\r\n  background: white;\r\n  border-radius: 5px;\r\n  transition: all 0.3s ease-in-out;\r\n  min-width: 45px;\r\n  min-height: 45px;\r\n  font-size: 1em;\r\n  text-decoration: none;\r\n  padding: 15px;\r\n  z-index: 99;\r\n  margin: 10px;\r\n  cursor: pointer;\r\n  animation-name: loading-toast-time;\r\n  animation-duration: 4s;\r\n}\r\n\r\n.success {\r\n  --color: hsl(108, 76%, 50%);\r\n  --shadow-color: hsl(108, 76%, 80%);\r\n}\r\n\r\n.error {\r\n  --color: hsl(10, 76%, 50%);\r\n  --shadow-color: hsl(0, 76%, 80%);\r\n}\r\n\r\n.info {\r\n  --color: hsl(200, 76%, 50%);\r\n  --shadow-color: hsl(200, 76%, 80%);\r\n}\r\n\r\n@keyframes loading-toast-time {\r\n  25% {\r\n    border-top: 3px solid var(--color);\r\n  }\r\n  50% {\r\n    border-right: 3px solid var(--color);\r\n  }\r\n  75% {\r\n    border-bottom: 3px solid var(--color);\r\n  }\r\n  100% {\r\n    border-left: 3px solid var(--color);\r\n    box-shadow: 0 0 10px var(--shadow-color);\r\n  }\r\n}\r\n\r\n.loading {\r\n  z-index: 999;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n.loading__container {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  gap: 10px;\r\n  min-height: 50px;\r\n}\r\n.loading__item {\r\n  width: 20px;\r\n  height: 40px;\r\n  border-radius: 10px;\r\n  background: grey;\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.loading__item:nth-child(1) {\r\n  animation: loading 1s infinite;\r\n}\r\n.loading__item:nth-child(2) {\r\n  animation: loading 1s infinite 0.2s;\r\n}\r\n.loading__item:nth-child(3) {\r\n  animation: loading 1s infinite 0.4s;\r\n}\r\n\r\n@keyframes loading {\r\n  0% {\r\n    height: 40px;\r\n  }\r\n  50% {\r\n    height: 20px;\r\n  }\r\n  100% {\r\n    height: 40px;\r\n  }\r\n}\r\n\r\n#likeButtonContainer {\r\n  position: fixed;\r\n  bottom: 0;\r\n  right: 0;\r\n  margin: 30px;\r\n  z-index: 99;\r\n}\r\n\r\n.like {\r\n  background: rgb(255, 255, 195);\r\n  box-shadow: 2px 4px 2px rgb(255, 197, 92);\r\n  display: inline-block;\r\n  border-radius: 5px;\r\n  transition: all 0.3s ease-in-out;\r\n  min-width: 45px;\r\n  min-height: 45px;\r\n  font-size: 1em;\r\n  border: white;\r\n  text-decoration: none;\r\n  padding: 3px;\r\n  cursor: pointer;\r\n}\r\n\r\n.like img {\r\n  width: 30px;\r\n}\r\n\r\n.empty {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  min-height: 200px;\r\n  margin: 10px;\r\n  font