;\r\n    margin: 10px 0;\r\n\r\n    li {\r\n      a {\r\n        display: flex;\r\n        flex-direction: column;\r\n        justify-content: center;\r\n        text-decoration: none;\r\n        color: $red-orange;\r\n        font-weight: 600;\r\n        min-width: 45px;\r\n        min-height: 45px;\r\n        &:hover {\r\n          color: $yellow-light;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\nmain {\r\n  display: flex;\r\n  justify-content: center;\r\n  article {\r\n    #hero {\r\n      display: flex;\r\n      flex-direction: column;\r\n      gap: 20px;\r\n      justify-content: center;\r\n      align-items: center;\r\n      text-align: center;\r\n      min-height: 400px;\r\n      width: 100vw;\r\n      max-width: 1200px;\r\n      background-image: url(\"../public/images/heros/hero-image_4-large.webp\");\r\n      background-color: $red-orange;\r\n      background-size: cover;\r\n      background-position: center;\r\n      background-repeat: no-repeat;\r\n      padding: 20px;\r\n      margin: 0px;\r\n\r\n      h1 {\r\n        color: $red-orange;\r\n        font-size: 4rem;\r\n        text-shadow: 2px 4px 2px rgba(255, 255, 0, 1);\r\n      }\r\n\r\n      .title-2 {\r\n        font-size: 2.5rem;\r\n      }\r\n\r\n      #search-restaurant {\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        gap: 20px;\r\n        padding: 20px;\r\n        margin: 0px;\r\n        max-width: 1200px;\r\n        font-size: 1.2rem;\r\n        text-align: center;\r\n        font-weight: 600;\r\n        margin-top: auto;\r\n        border-radius: 20px;\r\n        opacity: 0.75;\r\n      }\r\n    }\r\n\r\n    #restaurant-container {\r\n      display: grid;\r\n      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));\r\n      grid-gap: 20px;\r\n      padding: 20px;\r\n      margin: 0px;\r\n      width: 100vw;\r\n      max-width: 1200px;\r\n    }\r\n  }\r\n}\r\n\r\nfooter {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  background-color: $red-orange;\r\n  color: $yellow-light;\r\n  padding: 20px;\r\n  margin: 0px;\r\n  min-height: 50px;\r\n  width: 100vw;\r\n  font-size: 1.2rem;\r\n  text-align: center;\r\n  font-weight: 600;\r\n  margin-top: auto;\r\n}\r\n\r\n@media screen and (max-width: 425px) {\r\n  .nav {\r\n    flex-direction: column;\r\n\r\n    .nav__logo {\r\n      min-width: 100vw;\r\n    }\r\n\r\n    .hamburger {\r\n      display: inline !important ;\r\n      font-size: 2rem;\r\n      color: $red-orange;\r\n      border: none;\r\n      background: none;\r\n      transform: translateY(0%) !important;\r\n    }\r\n    .nav-menu {\r\n      position: absolute;\r\n      transform: translateY(-100%);\r\n      z-index: 998;\r\n      background: $orange;\r\n      padding: 5px;\r\n      margin: 0px;\r\n      min-width: 100vw;\r\n      justify-content: space-around;\r\n    }\r\n    .nav-menu.open {\r\n      transform: translateY(100%);\r\n      transition: transform 0.4s ease-in-out;\r\n    }\r\n  }\r\n  main {\r\n    article {\r\n      #hero {\r\n        background-image: url(\"../public/images/heros/hero-image_4-small.jpg\");\r\n        background-image: url(\"../public/images/heros/hero-image_4-small.webp\");\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
